@import url("https://fonts.googleapis.com/css2?family=PT+Sans:wght@700&family=Poppins:wght@700&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "PT Sans", sans-serif;
}

.square {
  background: rgba(3, 111, 158, 0.8);
  height: 5rem;
  width: 5rem;
  cursor: pointer;
  /*O X styles*/
  color: #fff;
  font-size: 4rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
.square:hover {
  background: rgba(3, 111, 158);
}
.square.clicked {
  background: rgba(2, 67, 95);
}

.App {
  height: 100vh;
  background: rgb(147, 192, 192);
  display: flex;
  justify-content: center;
  align-items: center;
}

.board {
  display: grid;
  grid-template-columns: repeat(3, auto);
  align-items: center;
  justify-content: center;
  grid-gap: 5px;
}

/*EndGame*/
.end-game-screen {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100vw;
  background: rgba(0, 0, 0, 0.8);
}
.end-game-screen .btn {
  background: rgb(27, 255, 255);
  color: rgb(63, 118, 170);
  border: 2px solid rgb(63, 118, 170);
  font-size: 2rem;
  margin-bottom: 1rem;
  padding: 10px 14px;
  cursor: pointer;
  border-radius: 5px;
  transition: 0.3s ease-out;
  max-height: 7rem;
}
.end-game-screen .btn:hover {
  background: rgb(63, 118, 170);
  color: rgb(27, 255, 255);
  border: 2px solid rgb(27, 255, 255);
}

.win-text {
  color: white;
  font-size: 5rem;
}

.win-history {
  color: white;
  font-size: 2rem;
  margin: 1rem 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}